@import "./icomoon/style.css";
@import "./help/font.scss";

$colorAppBg: linear-gradient(to bottom right, #17171b 0%, #28282f 100%);
$colorAppText: #d0d0d2;
$colorMain: #78cc6d;
$colorMainBg: #31313a;
$colorWhiteText: rgba(255, 255, 255, 0.85);
$colorText: rgba(255, 255, 255, 0.55);
$colorScrolLine: #65656c;
$colorLineVertical: radial-gradient(
  ellipse at top,
  rgba(197, 202, 213, 0.15) 0%,
  rgba(255, 255, 255, 0) 70%
);
$colorLineHorizontal: radial-gradient(
  ellipse at left,
  rgba(197, 202, 213, 0.15) 0%,
  rgba(255, 255, 255, 0) 70%
);
$colorSkillDissable: #3e4148;

body,
html,
#root,
.App {
  height: auto;
  min-height: 100%;
  display: flex;
  width: 100%;
}
div {
  box-sizing: border-box;
}
:focus {
  outline: 0;
}
body {
  overflow-x: hidden;
  display: block;
  position: relative;
  color: $colorAppText;
  @include Poppins();
}
.App {
  background: $colorAppBg;
  justify-content: center;
  align-items: center;
}
.main {
  max-width: auto;
  display: flex;
  justify-content: center;
  margin: 8vh 3vw;
  width: 100%;
  height: 84vh;
  max-width: 1268px;
  max-height: 674px;
  &__block {
    display: flex;
    justify-content: center;
    width: 100%;
  }
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 1em;
  line-height: 1;
  margin: 0;
}
* {
  outline: none;
  box-sizing: border-box;
  &::before,
  &::after {
    box-sizing: border-box;
  }
}
a {
  text-decoration: none;
  &:hover {
    text-decoration: underline;
  }
}
ul,
ol {
  list-style-type: none;
  list-style-position: inside;
  padding: 0;
  margin: 0;
}
p {
  margin: 0;
}
button {
  outline: none;
  &:focus {
    outline: none;
  }
}

img {
  max-width: 100%;
}

.map {
  height: 240px;
}

@media (max-height: 700px) {
  .main {
    margin-top: 6vh;
    margin-bottom: 6vh;
    height: 88vh;
  }
}

@import "ui/ui.scss";
@import "components/nav.scss";
@import "components/right-block.scss";
@import "block/CVBlock.scss";
@import "page/about.scss";
@import "page/resume.scss";
@import "page/contact.scss";
@import "./responsive.scss";
